import axios from "axios";

class TodoService {

    createTodo(requestBody){
        return axios.post("/api/1.0/todos/createTodo",requestBody)
    }

    deleteTodo(id){
       return axios.delete(`api/1.0/todos/deleteTodo?id=${id}`)
    }

    updateTodo(todoId,text){
        return axios.patch(`api/1.0/todos/updateTodo?text=${text}&todoId=${todoId}`)
    }

    GetAllTodosByUserId(userId){
        return axios.get(`/api/1.0/todos/GetAllTodosByUserId?userId=${userId}`)
    }

}

export default TodoService;