import { LOGIN_SUCCESS, LOGOUT_SUCCESS } from "./actionCostants"

const logOutState = {
    isLogin: false,
    username: undefined, 
    userId : undefined,
    email : undefined,
    surName : undefined
    
  }

  const authStateReducer =(state = {...logOutState},action) =>{
    if(action.type === LOGOUT_SUCCESS){
      return logOutState
    }else if(action.type ===LOGIN_SUCCESS){
        const data = action.payload
        const {userId,email,surName,fname} = data
        const payload = {
            username : fname,
            isLogin : true,
            userId ,
            email,
            surName 

        }
        return payload
    }
    return state
  }

  export default authStateReducer;