import React, { useState } from 'react'
import {Form, Container,Button,Col,Row, Alert} from "react-bootstrap"
import Input from '../components/Input'
import UserService from "../services/UserService"

export default function RegisterPage() {
    const userService = new UserService()
    const [fname,setFname] = useState("")
    const [surname,setSurname] = useState("")
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const [errors,setErrors] = useState({})
    const {fName,surName:surnameError,userPassword:passwordError,email:emailError} = errors;
    const [isRegister,setIsRegister] = useState(false)
    const onChange= (event) => {
    const { name, value } = event.target;
    const error = {...errors}
    switch (name) {
      case "fname":
        setFname(value);
        error.fName = undefined
        setErrors(error)
        break;
      case "surname":
        setSurname(value);
        error.surnameError = undefined
        error.surName = undefined
        setErrors(error)
        break;
      case "email":
        setEmail(value);
        error.emailError = undefined
        error.email = undefined
        setErrors(error)
        break;
      case "password":
        setPassword(value);
        error.passwordError = undefined
        error.userPassword = undefined
        setErrors(error)
        break;
    }
    }
    const onClickSignUp = async(e) =>{
        e.preventDefault();
        let requestBody={
            fname : fname,
            surName : surname,
            email,
            userPassword : password
        }
        try{
             await userService.createUser(requestBody)
             setIsRegister(true)
        }catch(error){
            if(error.response.data.validationErrors){
                setErrors(error.response.data["validationErrors"])
            }
        }
        
    }

    return (
        <Container className="w-25" >
      
        <Col className="text-center">
          <Row className="text-center">
            <h1>Sign Up</h1>
          </Row>
          </Col>
            <Form>
              <Input name="fname" error={fName} label="Name" onChange={onChange} />
              <Input name="surname" error={surnameError} label="Surname" onChange={onChange} />
              <Input name="email" error={emailError} label="Email" onChange={onChange} placeholder="app@todo"/>
              <Input name="password" error={passwordError} label="Password" onChange={onChange} type="password" />
              
              <Form.Group className="text-center mt-3">
                <Button onClick={onClickSignUp}>Sign Up</Button>
                {isRegister?<Alert variant='info'>Registered Successfully</Alert>:""}
              </Form.Group>
              
            </Form>

        
      </Container>
    )
}
