import React, { useState } from 'react'
import { ListGroup ,Button, ListGroupItem, Container} from "react-bootstrap"
import TodoService from '../services/TodoService';
import Input from './Input';
export default function Task(props) {
    const {text , id} = props;
    const [textEdit,setTextEdit] = useState(text)
    const [isEdit,setIsEdit] = useState()

    const todoService = new TodoService();

    const onClickEdit = (e)=>{
        e.preventDefault();
        setIsEdit(true)
    }

    const onClickDelete = async(e)=>{
        e.preventDefault();
        await todoService.deleteTodo(id);
    }
    const onClickSave = async(e) =>{
        e.preventDefault();
        
        try{
            await todoService.updateTodo(id,textEdit)
            setIsEdit(false)
            
        }catch(error){

        }
        
        
    }

    const onChangeInput = (event)=>{
        setTextEdit(event.target.value)
    }
    let view1=<ListGroup.Item className='m-1'>
                <p>{text}</p>
                
                <div>
                <Button className='me-1' onClick={onClickEdit}>Edit</Button>
                <Button onClick={onClickDelete}>Delete</Button>
                </div>
            </ListGroup.Item>
    let view2 = <ListGroup.Item className='m-1'>
                <Input onChange={onChangeInput} value={textEdit}/>
                <Button onClick={onClickSave}>Save</Button>
            </ListGroup.Item>
    return (
        <Container>
        {!isEdit? view1:view2}
            </Container>
        
            
        
    )
}
