import React, { useState ,useEffect} from "react";
import { connect } from "react-redux";
import {Button, Container, ListGroup} from "react-bootstrap"
import Input from "../components/Input";
import Task from "../components/Task";
import TodoService from "../services/TodoService";
function TodoPage(props) {
  let todoService = new TodoService();
  const { userId } = props;
  const [text, setText] = useState();
  const [todos, setTodos] = useState([]);
    
  useEffect(() => {
      todoService.GetAllTodosByUserId(userId).then((response) => setTodos(response.data))
  }, [todos])
  const onChangeTodo = (event) => {
    setText(event.target.value);
  };

  const onClickCreate = async(e)=>{
      e.preventDefault();
      let requestBody = {
          text,
          user : {
              userId
          }
      }
      try{
        if(text !== ""){
          await todoService.createTodo(requestBody)
          let response = await todoService.GetAllTodosByUserId(userId)
          setTodos(response.data)
          setText("")
          console.log("burdaymm")
        }

          
      }catch(error){

      }
      
      
  }

  return (
    <Container className="w-50">
      
      <Input label="Enter your todo" name="todo" onChange={onChangeTodo} value={text} />
      <Button onClick={onClickCreate}>Create Todo</Button>
      <ListGroup className="mt-2">
        
          {todos.map((e) => {
        return <Task text = {e.text} id={e.id}/>
      })}
      </ListGroup>
      
      
    </Container>
  );
}
const putStatesToProps = (storeStates) => {
  return {
    userId: storeStates.userId,
  };
};
export default connect(putStatesToProps)(TodoPage);
