import { createStore } from "redux";
import authStateReducer from "./authStateReducer";
import SecureLS from "secure-ls";

const secureLs = new SecureLS()

const getStatesFromLocal = () => {
  const appAuth = secureLs.get("app-auth");
  let defaultState = {
    isLogin: false,
    username: undefined, 
    userId : undefined,
    email : undefined,
    surName : undefined
  };
  if (appAuth) {
      defaultState = appAuth
    
  }
  return defaultState
};

const setStatesToLocal = (storeStates) => {
    secureLs.set("app-auth", storeStates);
}


const configStore = () => {
  const store = createStore(
    authStateReducer,
    getStatesFromLocal(),
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
  );
  store.subscribe(() => {
    setStatesToLocal(store.getState())
  });
  return store;
};

export default configStore;
